import axios from 'axios';
import React from 'react';
import Items from './items';

const Main = ({ items, setItems }) => {
  return (
    <>
      <section className="jumbo">
        <h1>YonaYoga</h1>
        <p>
          Using a series of utilities, you can create this jumbotron, just like the one in
          previous versions of Bootstrap. Check out the examples below for how you can
          remix and restyle it to your liking.
        </p>
        <button type="button" className="btn btn-primary">
          Primary
        </button>
      </section>
      <Items items={items} />
      <div style={{ display: 'flex', justifyContent: 'center', margin: '20px' }}>
        <button
          className="btn btn-primary"
          onClick={() => {
            axios
              .get('https://codingapple1.github.io/shop/data2.json')
              .then((res) => {
                res.data = res.data.map(
                  (v) =>
                    (v = {
                      ...v,
                      id: v.id + 3,
                      desc: '더보기용',
                      img: `${v.id - 2}.jpeg`,
                    })
                );
                const newData = [...items, ...res.data];
                setItems(newData);
              })
              .catch((err) => {
                console.log(err);
              });
          }}
        >
          더보기
        </button>
      </div>
    </>
  );
};

export default Main;
